<script>

import goodsExchangePopup from './inGoodsBody/goodsExchangePopup.vue';

export default {
	components: {
		goodsExchangePopup,
	},
	data() {
		return {
			loading: true,
			requestParam: {
				id: 0,
			},
			bodyData: {
			},
			statusBarHeight: 0,
			swiperCurrent: 0,
			attrMap: {},
			pHeightMap: {},
		}
	},
	onLoad(option) {
		this.loading = true;
		this.statusBarHeight = uni.getSystemInfoSync().statusBarHeight;
		console.log(option);
		this.requestParam.id = option.id;
		this.getAttrMap();
		this.getBodyData();
	},
	methods: {
		openGoodsPopup() {
			if(this.bodyData!=null){
				this.$refs.goodsExchangePopup.open(this.bodyData);
			}

        },
		swiperChange(e) {
			this.swiperCurrent = e.detail.current;
		},

		async getBodyData() {

			// 从缓存中获取
			let goodsById = uni.getStorageSync('goodsById' + this.requestParam.id);
			if (goodsById) {
				this.bodyData = goodsById;
				this.loading = false;
				console.log(JSON.stringify(this.bodyData));
			}

			await new Promise((resolve, reject) => {

				this.$kt.request.request({
					api: this.$api.business.ovoShop.goods.getById,
					data: this.requestParam,
					stateSuccess: (res) => {
						this.bodyData = res.data;
						this.bodyData['bodyFileList'] = JSON.parse(this.bodyData.bodyFileListStr);
						console.log(JSON.stringify(this.bodyData));

						// 存进缓存
						uni.setStorageSync('goodsById' + this.requestParam.id, this.bodyData);
						// 存进缓存时间
						uni.setStorageSync('goodsById' + this.requestParam.id + 'Time', new Date().getTime());
						this.loading = false;
						resolve();
					}
				})

			});

		},

		async getAttrMap() {
			await new Promise((resolve, reject) => {
				var attrMap = uni.getStorageSync('attrMap');
				if (attrMap != null && attrMap != '') {
					this.attrMap = attrMap;
					return null;
				}

				var attrMapTime = uni.getStorageSync('attrMapTime');
				if (attrMapTime != null && attrMapTime != '') {
					var nowTime = new Date().getTime();
					// 24小时内有效
					if (nowTime - attrMapTime < 1000 * 60 * 10) {
						this.attrMap = attrMap;
						resolve();
						return null;
					}
				}

				this.$kt.request.request({
					api: this.$api.business.ovoShop.goodsAttr.getMap,
					data: this.requestParam,
					stateSuccess: (res) => {
						this.attrMap = res.data;
						// 存入缓存
						uni.setStorageSync('attrMap', this.attrMap);
						// 将缓存时间存入缓存
						uni.setStorageSync('attrMapTime', new Date().getTime());
						resolve();

					}
				})
			});
		}
	}
}
</script>

